<style lang="scss">
    .must {
        color: red
    }
</style>
<template>
    <div class="card card-block">
        <form class="form-horizontal j-forms">
            <h5></h5>
            <div class="row form-group form-group-sm">
                <label class="col-md-1"><span class="must">*</span></label>
                <div class="col-md-2">
                    <el-input placeholder="" v-model="vhlowner.c_owner_nme">
                </div>
                <label class="col-md-1"><span class="must">*</span></label>
                <div class="col-md-2">
                    <el-select v-model="vhlowner.c_owner_cls" id="vhlowner.c_owner_cls" clearable placeholder="">
                        <el-option v-for="item in codelist.c_owner_cls" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </div>
                <label class="col-md-1"><span class="must">*</span></label>
                <div class="col-md-2">
                    <el-select v-model="vhlowner.c_certf_cls" clearable placeholder="">
                        <el-option v-for="item in codelist['c_certf_cls'][vhlowner.c_owner_cls]" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </div>
                <label class="col-md-1"><span class="must">*</span></label>
                <div class="col-md-2">
                    <el-input v-model="vhlowner.c_certf_cde"></el-input>
                </div>
            </div>
            <div class="row form-group form-group-sm">
                <div class="col-md-12" style="text-align:center">
                    <el-button type="primary" @click="searchCustomer">
                        
                    </el-button>
                    <el-button type="primary" @click="resetCustomer">
                        
                    </el-button>
                    <el-button type="primary" @click="syncFromInsured">
                        
                    </el-button>
                </div>
            </div>
        </form>
    </div>
</template>
<script>
import ElSelect from 'components/select';
import ElOption from 'components/option';
import ElInput from 'components/input';
import ElButton from 'components/button';
import ElTag from 'components/tag';
import ElSwitch from 'components/switch';
import ElDatePicker from 'components/date-picker';
import Tooltip from 'components/tooltip';
import PolicyUtil from '../util/PolicyUtil';
export default {
  name: 'policy-vhlowner',
  props:{},
  components:{
    ElSelect, 
    ElOption, 
    ElInput, 
    ElSwitch,
    ElButton, 
    ElTag,
    ElDatePicker,
    Tooltip
  },
  data () {
    return {
      "vhlowner" : {
        "c_upd_cde" : "",
        "c_owner_nme" : "",
		"c_resid_addr" : "",
		"c_certf_cde" : "",
		"c_owner_cls" : "",
		"t_crt_tm" : "",
		"t_birthday" : "",
		"c_certf_cls" : "",
		"c_crt_cde" : "",
		"c_app_no" : "",
		"c_clnt_addr" : "",
		"t_upd_tm" : "",
		"c_sex" : ""
      },
      "codelist" : {
        "c_owner_cls" : [
          {"value" : "1", "label" : ""},
          {"value" : "2", "label" : ""},
          {"value" : "3", "label" : ""}
        ],
        "c_certf_cls" : {
          "1" : [
            {"value" : "120001","label" : ""},
            {"value" : "120002","label" : ""},
            {"value" : "120003","label" : ""},
            {"value" : "120009","label" : ""}
          ],
          "2" : [
            {"value" : "110001","label" : ""},
            {"value" : "110002","label" : ""},
            {"value" : "110003","label" : ""},
            {"value" : "110009","label" : ""}
          ],
          "3" : [
            {"value" : "110001","label" : ""},
            {"value" : "110002","label" : ""},
            {"value" : "110003","label" : ""},
            {"value" : "110009","label" : ""}
          ]
        }
      }
    }
  },
  "methods" : {
    resetCustomer(){
      var vhlowner = this._data.vhlowner;
      PolicyUtil.resetModel(vhlowner);
    },
    searchCustomer(){
      var vhlowner = this._data.vhlowner;
      PolicyUtil.searchCustomer(vhlowner);
    },
    syncFromInsured(){
      alert("^_^!");
    }
  }
}
</script>


